import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			pageNo: 1,
			pageSize: 10,
			coupons: [],
			total: 0,
			loadingType: ''
		};
	},

	onLoad(options) {
		this.searchCoupon();
	},
	onReachBottom(){
		if(this.loadingType === 'more'){
			this.pageNo = this.pageNo + 1;
			this.searchCoupon();
		}
	},
	computed: {
		...mapState(['hasLogin', 'userInfo', 'footPrint'])
	},
	methods: {
		//搜索优惠券
		searchCoupon() {
			let postData = {
				keyArray: ['ACTIVE'],
				active: true,
				startIndex: (this.pageNo - 1) * this.pageSize,
				pageSize: this.pageSize
			};
			//加载中
			this.loadingType = 'loading';
			this.$api.request.couponList(postData, res => {
				if (res.body.status.statusCode === '0') {
					var coupons = res.body.data.coupons;
					this.coupons = this.coupons.concat(coupons);
					this.total = res.body.data.total;
					this.loadingType = this.coupons.length >= this.total ? 'noMore' : 'more';
				} else {
					this.loadingType = 'more';
				}
			}, true);
		},
		//领取优惠券
		getCoupon(item) {
			this.$api.request.getCoupon({
				actionType:'RECEIVE',
				couponDTO:{
					couponUuid: item.couponUuid,
				},
				userDTO: {
					userUuid: this.userInfo.userUuid
				},
				receiveChannel: 'SELF'
			}, res => {
				if (res.body.status.statusCode === '0') {
					this.$api.msg('领取成功');
				} else {
					this.$api.msg(res.body.status.errorDesc);
				}
			}, false);
		},
		resetPage() {
			this.coupons = [];
			this.total = 0;
			this.pageNo = 1;
		},
		//详情
		navToDetailPage(item) {
			let id = item.productUuid;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		}
	},
}
